.bill__value-box {
  width: 380px;
  background-color: #f3f9fa;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.bill__value-input {
  color: #5e7a7d;
  font-size: 16px;
  font-weight: 700;
  font-family: sans-serif;
  background-color: #f3f9fa;
  border: none;
  text-align: right;
  outline: none;
}

.bill-text {
  font-size: 16px;
  font-weight: 700;
  font-family: sans-serif;
  color: #5e7a7d;
}
.bill__value-icone {
  padding-left: 19px;
}

@media (max-widht: 480px) {
  .bill-value {
    padding-top: 20px;
  }
}
